import { createNativeStackNavigator } from "@react-navigation/native-stack";
import React from "react";
import { Favorite, Home, Post } from "~/common/screens";
import { COLOR_primary, COLOR_light } from "@env";
import ZawgyiUnicodeConverter from "~/common/components/ZawgyiUnicodeConverter";

const Stack = createNativeStackNavigator();

export function BookStackNavigation() {
  return (
    <Stack.Navigator
      screenOptions={{
        headerShown: false,
      }}
    >
      <Stack.Screen name="Book" component={Home} />
      <Stack.Screen name="BookPost" component={Post} />
    </Stack.Navigator>
  );
}

export function FavoriteStackNavigation() {
  return (
    <Stack.Navigator
      screenOptions={{
        headerTintColor: COLOR_light,
        headerStyle: {
          backgroundColor: COLOR_primary,
          headerRight: () => <ZawgyiUnicodeConverter />,
        },
      }}
    >
      <Stack.Screen
        name="Favorite"
        component={Favorite}
        options={{ headerShown: false }}
      />
      <Stack.Screen
        name="FavoritePost"
        component={Post}
        options={({ route }) => ({ title: route.params?.title })}
      />
    </Stack.Navigator>
  );
}
