import React from "react";
import { View } from "react-native";

const Fallback = React.memo(() => <View style={{ flex: 1 }} />, []);

const LazyHome = React.lazy(() => import("./tab/Home"));

export const Home = () => (
  <React.Suspense fallback={<Fallback />}>
    <LazyHome />
  </React.Suspense>
);

const LazyImages = React.lazy(() => import("./tab/Images"));

export const Images = () => (
  <React.Suspense fallback={<Fallback />}>
    <LazyImages />
  </React.Suspense>
);

const LazyFavorite = React.lazy(() => import("./tab/Favorite"));

export const Favorite = () => (
  <React.Suspense fallback={<Fallback />}>
    <LazyFavorite />
  </React.Suspense>
);

const LazyPost = React.lazy(() => import("./other/Post"));

export const Post = () => (
  <React.Suspense fallback={<Fallback />}>
    <LazyPost />
  </React.Suspense>
);

export { default as ImageViewer } from "./modals/ImageViewer";
