import React from "react";
import useStorage from "./useStorage";

const settingsContext = React.createContext();

export default function useSettings() {
  return React.useContext(settingsContext);
}

const initialState = {
  dark: false,
  font: "zawgyi",
  rate: 1.19,
  shouldPlay: false,
  loading: true,
};

export function SettingsContextProvider({ children }) {
  const [state, setState] = React.useState(initialState);
  const { getItem, setItem } = useStorage("@settings");

  // Do On Start of App
  const readFromStorage = React.useCallback(() => {
    const item = getItem();
    if (item) {
      return setState((prev) => ({ ...prev, ...item, loading: false }));
    }
    return setState((prev) => ({ ...prev, loading: false }));
  }, []);

  React.useEffect(() => readFromStorage(), []);

  // Do When State Changes
  React.useEffect(() => {
    !state.loading && setItem(state);
  }, [state]);

  // Methods
  const changeTheme = React.useCallback(() => {
    setState((prev) => ({ ...prev, dark: !state.dark }));
  }, []);

  const changeFont = React.useCallback(() => {
    setState((prev) => ({
      ...prev,
      font: prev.font === "zawgyi" ? "unicode" : "zawgyi",
    }));
  }, []);

  const value = {
    changeTheme,
    changeFont,
    dark: state.dark,
    font: state.font,
    focus: state.font,
    rate: state.rate,
    shouldPlay: state.shouldPlay,
    loading: state.loading,
  };

  return (
    <settingsContext.Provider value={value}>
      {children}
    </settingsContext.Provider>
  );
}
