import React from "react";
import useSettings from "~/utils/hooks/useSettings";

export default function ZawgyiUnicodeConverter() {
  const { font, changeFont } = useSettings();

  return (
    <button
      className="btn text-capitalize btn-success btn-sm border-light border-1 border me-2"
      onClick={changeFont}
    >
      {font}
    </button>
  );
}
