import React from "react";
import { createBottomTabNavigator } from "@react-navigation/bottom-tabs";
import EIcon from "~/common/elements/EIcon";
import { BookStackNavigation, FavoriteStackNavigation } from "../stack";
import { createNativeStackNavigator } from "@react-navigation/native-stack";
import { Images, ImageViewer } from "~/common/screens";

const Tab = createBottomTabNavigator();
const Stack = createNativeStackNavigator();

export default function TabNavigation() {
  return (
    <Stack.Navigator
      screenOptions={{
        headerShown: false,
      }}
    >
      <Stack.Screen name="Tabs" component={Tabs} />
      <Stack.Screen name="ImageViewer" component={ImageViewer} />
    </Stack.Navigator>
  );
}

function Tabs() {
  return (
    <Tab.Navigator
      screenOptions={{ headerShown: false }}
      initialRouteName={"Books"}
    >
      <Tab.Screen
        name={"Books"}
        component={BookStackNavigation}
        options={{
          tabBarIcon: ({ focused, color, size }) => (
            <EIcon
              name={focused ? "book-fill" : "book"}
              color={color}
              size={size}
            />
          ),
        }}
      />
      <Tab.Screen
        name={"Images"}
        component={Images}
        options={{
          tabBarIcon: ({ _, color, size }) => (
            <EIcon name={"images"} color={color} size={size} />
          ),
        }}
      />
      {/* <Tab.Screen
        name={"Favorites"}
        component={FavoriteStackNavigation}
        options={{
          tabBarIcon: ({ focused, color, size }) => (
            <EIcon
              name={focused ? "heart-fill" : "heart"}
              color={color}
              size={size}
            />
          ),
        }}
      /> */}
    </Tab.Navigator>
  );
}
